.homework .ant-menu-inline {
  min-height: calc(100vh - 180px);
}
.homework-header {
  text-align: center;
  font-size: 16px;
  background: #fff;
  border-radius: 5px;
  letter-spacing: 1px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 9;
  top: 0;
}
.homework-header .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
  cursor: pointer;
}
.homework-header a {
  color: #fff;
}
.homework-header div:nth-of-type(2) {
  font-size: 18px;
  font-weight: 500;
  color: #2A283D;
  font-family: Alibaba PuHuiTi;
}
.homework-header .ant-btn {
  background: #3E78ED;
  color: #fff;
  border: none;
  box-shadow: none;
}
.homework-main {
  margin: 30px 30px 0 41px;
  display: flex;
  padding: 60px 0 0;
}
.homework-main .homework-main-left {
  min-width: 350px;
  background: #fff;
  margin-right: 25px;
  padding: 0 10px;
}
.homework-main .homework-main-left h2 {
  color: #2A283D;
  text-align: left;
  padding: 15px 10px;
  border-bottom: 1px solid #F3F3F3;
}
.homework-main .homework-main-left h2 span {
  float: right;
}
.homework-main .homework-main-left h2::before {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  background: #3E78ED;
  border-radius: 50%;
  margin-right: 10px;
  vertical-align: middle;
}
.homework-main .homework-main-left .student-menu {
  color: #333;
  padding: 0 20px 0 36px !important;
}
.homework-main .homework-main-left .student-menu span {
  margin-right: 15px;
}
.homework-main .homework-main-left .student-menu span:last-child {
  float: right;
  vertical-align: middle;
  margin: 0;
}
.homework-main .homework-main-left .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: #F7F9FB !important;
}
.homework-main .homework-main-right {
  flex: 1;
  height: calc(100vh - 105px);
  overflow-y: auto;
  background: #fff;
}
.homework-main .homework-main-right .grade_read_l {
  color: #fff;
  background: #3E78ED;
  margin-left: 114px;
}
.homework-main .homework-main-right .ant-form-item-children {
  display: flex;
  align-items: center;
}
.homework-main .homework-main-right .ant-form-item-children > div {
  margin-right: 10px;
}
.homework-main .homework-main-right h2 {
  margin: 10px 0;
}
.homework-main .homework-main-right p {
  margin: 20px 0;
}
.homework-main .homework-main-right > .ant-row {
  background: #Fff;
  padding: 30px;
  margin-bottom: 20px;
  border-radius: 5px;
}
.homework-main .homework-main-right > .ant-row > h2 {
  color: #2A283D;
  opacity: 0.99;
  font-size: 18px;
  font-family: Alibaba PuHuiTi;
}
.homework-main .homework-main-right > .ant-row > div {
  padding-left: 33px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  padding-bottom: 47px;
}
.homework-main .homework-main-right > .ant-row > div p {
  color: #2A283D;
  opacity: 0.99;
  font-family: Alibaba PuHuiTi;
  font-size: 16px;
  margin-top: 28px;
}
.homework-main .homework-main-right > .ant-row > div:last-child {
  border: none;
}
.homework-main .homework-main-right > .ant-row .radio-option {
  max-width: calc(100% - 300px);
  min-width: 256px;
  line-height: 44px;
  border-radius: 5px;
  padding: 0 27px;
  margin-bottom: 9px;
}
.homework-main .homework-main-right > .ant-row .student-answer {
  padding: 16px 26px;
  display: inline-block;
  width: 500px;
}
.homework-main .homework-main-right > .ant-row .student-answer > span {
  margin-right: 12px;
}
.homework-main .homework-main-right > .ant-row .student-answer > span span {
  margin-right: 5px;
}
.homework-main .homework-main-right > .ant-row .student-answer {
  width: calc(100% - 300px);
}
.homework-main .homework-main-right > .ant-row .student-answer > span {
  word-break: break-all;
  white-space: normal;
  word-wrap: break-word;
}
.homework-main .homework-main-right > .ant-row .ant-radio-checked .ant-radio-inner {
  border: 1px solid #3E78ED;
}
.homework-main .homework-main-right > .ant-row .ant-radio-checked .ant-radio-inner::after {
  background: #3E78ED;
}
.homework-main .homework-main-right > .ant-row .ant-radio-disabled + span {
  color: #2A283D !important;
  font-family: Alibaba PuHuiTi;
}
.homework-main .homework-main-right > .ant-row .ant-radio-wrapper {
  margin: 10px 0;
}
.homework-main .homework-main-right .ant-checkbox-group-item {
  display: block;
  padding-left: 30px;
  margin-bottom: 20px;
}
.homework-main .homework-main-right .topicAnswer {
  width: calc(100% - 300px);
  background: #F8F8F8;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 10px 26px 15px;
}
.homework-main .homework-main-right .topicAnswer .ant-col {
  color: #2A283D;
  font-family: Alibaba PuHuiTi;
}
.homework-main .homework-main-right .topicAnswer .ant-col span {
  line-height: 200%;
}
.homework-main .homework-main-right .topicAnswer .ant-row .ant-col:nth-of-type(1) span {
  font-weight: 600;
}
.homework-main .homework-main-right .topicAnswer img {
  position: absolute;
  right: 0;
  bottom: 0;
}
.homework-main .homework-main-right .homework-grade {
  margin-top: 15px;
}
.homework-main .homework-main-right .homework-grade .ant-row textarea {
  min-height: 130px;
}
.homework-main .homework-main-right .homework-grade .ant-form-item-required {
  min-width: 130px;
}
.homework-main .homework-main-right .homework-grade .ant-input-number,
.homework-main .homework-main-right .homework-grade .ant-form-item-control {
  width: 330px;
}
